// src/components/common/HamburgerMenu.tsx
import React, { useState } from 'react';
import styled from 'styled-components';

interface MenuProps {
  isOpen: boolean;
}

const MenuOverlay = styled.div<MenuProps>`
  position: fixed;
  top: 0;
  right: 0;
  height: 100vh;
  width: ${props => props.isOpen ? '280px' : '0'};
  background-color: white;
  box-shadow: ${props => props.isOpen ? '-5px 0 15px rgba(0, 0, 0, 0.1)' : 'none'};
  transition: width 0.3s ease;
  overflow: hidden;
  z-index: 1000;
  font-family: 'Montserrat', sans-serif;
`;

const MenuContent = styled.div`
  padding: 20px;
  width: 280px;
  overflow-y: auto;
  height: 100%;
`;

const AppInfo = styled.div`
  padding-bottom: 15px;
  margin-bottom: 20px;
  border-bottom: 1px solid #eee;
`;

const AppName = styled.h2`
  margin: 0;
  color: #38a3a5;
  font-size: 1.5rem;
`;

const AppVersion = styled.p`
  margin: 5px 0 0;
  color: #888;
  font-size: 0.9rem;
`;

const MenuItem = styled.div`
  padding: 12px 0;
  color: #333;
  font-size: 1.1rem;
  cursor: pointer;
  transition: color 0.2s;
  
  &:hover {
    color: #38a3a5;
  }
`;

const SubMenuItem = styled.div`
  padding: 10px 0 10px 20px;
  color: #555;
  font-size: 1rem;
  cursor: pointer;
  transition: color 0.2s;
  
  &:hover {
    color: #38a3a5;
  }
`;

const SectionDivider = styled.div`
  height: 1px;
  background-color: #eee;
  margin: 15px 0;
`;

interface HamburgerMenuProps {
  isOpen: boolean;
  onClose: () => void;
}

const HamburgerMenu: React.FC<HamburgerMenuProps> = ({ isOpen, onClose }) => {
  const [expandedItems, setExpandedItems] = useState<Record<string, boolean>>({});
  
  const toggleItem = (item: string) => {
    setExpandedItems(prev => ({
      ...prev,
      [item]: !prev[item]
    }));
  };
  
  return (
    <>
      {isOpen && (
        <div 
          style={{ 
            position: 'fixed', 
            top: 0, 
            left: 0, 
            right: 0, 
            bottom: 0, 
            background: 'rgba(0,0,0,0.3)', 
            zIndex: 999 
          }}
          onClick={onClose}
        />
      )}
      
      <MenuOverlay isOpen={isOpen}>
        <MenuContent>
          <AppInfo>
            <AppName>EmotiBot</AppName>
            <AppVersion>Version 1.0</AppVersion>
          </AppInfo>
          
          {/* User Profile Section */}
          <MenuItem onClick={() => toggleItem('userProfile')}>
            User Profile {expandedItems.userProfile ? '' : ''}
          </MenuItem>
          {expandedItems.userProfile && (
            <>
              <SubMenuItem>Personal Settings</SubMenuItem>
              <SubMenuItem>History</SubMenuItem>
              <SubMenuItem>Log Out</SubMenuItem>
            </>
          )}
          
          {/* Reports Section */}
          <MenuItem onClick={() => toggleItem('reports')}>
            Reports {expandedItems.reports ? '' : ''}
          </MenuItem>
          {expandedItems.reports && (
            <>
              <SubMenuItem>Reflect Reports</SubMenuItem>
              <SubMenuItem>Grow Reports</SubMenuItem>
            </>
          )}
          
          {/* App Settings Section */}
          <MenuItem onClick={() => toggleItem('appSettings')}>
            App Settings {expandedItems.appSettings ? '' : ''}
          </MenuItem>
          {expandedItems.appSettings && (
            <>
              <SubMenuItem>Data Management</SubMenuItem>
              <SubMenuItem>Notifications</SubMenuItem>
              <SubMenuItem>Privacy</SubMenuItem>
              <SubMenuItem>Language</SubMenuItem>
              <SubMenuItem>Theme</SubMenuItem>
              <SubMenuItem>Integrations</SubMenuItem>
            </>
          )}
          
          <MenuItem>Premium Features</MenuItem>
          <MenuItem>About</MenuItem>
          
          <SectionDivider />
          
          <MenuItem>Help</MenuItem>
          <MenuItem>Feedback</MenuItem>
        </MenuContent>
      </MenuOverlay>
    </>
  );
};

export default HamburgerMenu;